@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap');

.App {
  font-family: 'Poppins', sans-serif;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-self: center;
  background: #52079D;
  background-image: linear-gradient(white -300%, #52079D 60%, white 350%);
  margin: -8px;
  min-height: 100vh;
  height: max-content;
  /* padding-top: 40px;
  padding-bottom: 40px; */
}

.App2 {
  font-family: 'Poppins', sans-serif;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-self: center;
  backdrop-filter: blur(2px);
  min-height: 100vh;
}

.cellStyle {
    width: 40px;
    height: 40px;
    font-weight: 800;
    font-size: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
}

#gameOverImage {
    opacity: 100;
    background-position: center;
    max-width: 100%;
    margin: 0px 50px;
    margin-top: 100px;
    margin-bottom: 30px;
    background-image: url("https://www.google.com/logos/fnbx/minesweeper/lose_screen.png");
    /* background-repeat: repeat-x; */
    position: relative;
    /* padding-top: 20px; */
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    border-radius: 8px;
  }
  
  .tryAgain {
    cursor: pointer;
    margin: 0 50px;
    border-radius: 7px;
    background: darkgreen;
    color: rgb(158, 255, 158);
    padding: 10px;
    font-weight: 700;
  }